$(document).ready(function() {
  const hiddenName = 'hidden';

  const slickActiveClassName = '.slick-active';

  const $slick = $('.js-slider');

  function initSlider() {
    if ($slick.hasClass('slick-initialized')) {
      $slick.slick('unslick');
    }

    $slick.slick({
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      dots: true,
    });
  }

  initSlider();
});
